.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 140px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

ul {
  margin: 5px;
  padding: 0;
}

h4 {
  margin: 10px;
}

.no-dot {
  list-style-type: none;
}

.height-restricted {
  max-height: 150px
  
}

.my-container {

}

.flex-parent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.flex-button-parent{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.my-flex-button {
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.buttext {
  white-space: nowrap;
}

.my-flex {
  
}

.my-flex:hover {
  cursor: pointer;
}

.gimme-space {
  margin: 20px;
}

.gimme-lil-space {
  margin: 5px;
}

.why-no-click {
  position: relative;
  display: inline-block;
}
.why-no-click .here-why {
  top: 100%;
  left: 50%; 
  margin-left: -60px;
  visibility: hidden;
  font-size: 12px;
  color: #333;
  text-align: center;
  border-radius: 3px;
  border: 1px solid gray;

  position: absolute;
  z-index: 1;
}

.why-no-click .here-why::after {
  content: " ";
  position: absolute;
  bottom: 100%;  /* At the top of the tooltip */
  left: 50%;
  border-style: solid;
  border-color: transparent transparent #333 transparent;
}

.why-no-click:hover .here-why {
  visibility: visible;
}

.small-font {
  font-size: 14px;
}


